import { Component, ChangeDetectorRef, ElementRef, OnInit, Input, NgZone } from '@angular/core';

@Component({
  selector: 'item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.css']
})
export class ItemComponent implements OnInit {
  name: string;
  imgSrc: string;
  url: string;
  github: string;

  dontshow = ['ehem', 'portfolio', 'Bible', 'noamalffasy.github.io', 'treehouse-portfolio'];

  @Input() repo;
  // @Input('name') nameAttr: string;
  // @Input('url') urlAttr: string;
  // @Input('github') githubAttr: string;

  constructor(private elem: ElementRef) { }

  ngOnInit() {
    this.calcHeight();
  }

  calcHeight() {
    const visit = (document.querySelector('.visit') as HTMLElement);
    const height = document.querySelector('.card img').clientHeight.toString() + 'px';
    const visitLinks = document.querySelectorAll('.visit .link')

    visit.style.height = height;

    for (let i = 0; i < visitLinks.length; i++) {
      let elem = (visitLinks[i] as HTMLElement);
      // console.log(visit.style.height)
      elem.style.lineHeight = '16rem';
    }

    this.dontShow();
  }

  getName(name: String) {
    return name.split('-').join(' ');
  }

  getImg(name: String) {
    return "/assets/img/" + name.split(' ').join('-') + ".png";
  }

  dontShow() {
    for (let i = 0; i < this.dontshow.length; i++) {
      const name = this.dontshow[i];
      if(this.repo.name === name) {
        this.elem.nativeElement.style.visibility = 'collapse'
      }
    }
  }
}



