import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Http } from '@angular/http';
// import { ActivatedRouteSnapshot, RouterStateSnapshot, Resolve, Router } from "@angular/router";

import 'rxjs/Rx';
import { Observable } from 'rxjs/Observable';

import { RepoService } from "../repo.service"

@Component({
  moduleId: module.id,
  selector: 'category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {
  name: string;
  isProjects: Boolean = true;
  repos;

  @ViewChild('container') container: ElementRef;
  @ViewChild('title') title: ElementRef;
  @ViewChild('wrapper') wrapper: ElementRef;

  constructor(private repoService: RepoService, private elem: ElementRef, private http: Http) {
    this.name = elem.nativeElement.getAttribute('name');
    this.getRepos()
  }

  getRepos() {
    this.repoService
      .getRepos()
      .then(repos => {
        this.repos = repos
        console.log(this.repos)
        // this.calcHeight()
      });
  }

  ngOnInit() {
    if (this.name !== 'Projects') {
      const name = this.name;
      this.isProjects = false;
    }
  }

  calcHeight() {
    const items = document.querySelectorAll('item');
    for (let i = 0; i < items.length; i++) {
      const visit = document.querySelectorAll('.visit')[i] as HTMLElement;
      const height = document.querySelectorAll('.card img')[i].clientHeight.toString() + 'px';
      const visitLinks = document.querySelectorAll('.visit .link');

      visit.style.height = height;

      for (let j = 0; j < visitLinks.length; j++) {
        let elem = visitLinks[j] as HTMLElement;
        elem.style.lineHeight = '16rem';
      }
    }
  }
}



